@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Montserrat:wght@400;500;600;700;800;900&family=Roboto:wght@400;500;700;900&family=Rubik:wght@400;500;600;700;800&display=swap");

:root {
  --primary_color: #fbf500;
  --secondary_color: #222327;
  --black_color: #000;
  --pe_orng: #fbb500;
  --pe_blue: #4358ff;
  --pe_text_color: #56575a;
  --pe_green: #25df07;
  --font_montserrat: "Montserrat", sans-serif;
  --font_bebas_neue: "Bebas Neue", cursive;
  --font_roboto: "Roboto", sans-serif;
  --font_rubik: "Rubik", sans-serif;
  --transition: all ease-in-out 0.3s;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 15px;
  font-family: var(--font_montserrat);
  color: var(--black_color);
  background-color: #f1f1f1;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 700;
}

a,
.nav-link {
  text-decoration: none;
  color: var(--secondary_color);
  transition: var(--transition);
}

a:hover,
.nav-link:hover {
  color: var(--primary_color);
}

li,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.btn:focus,
.btn:active,
.form-control:focus,
.form-select:focus {
  box-shadow: none;
}

.btn_dark {
  background-color: var(--secondary_color);
  color: var(--bs-white);
}

.btn_dark:hover {
  background-color: var(--primary_color);
  color: var(--secondary_color);
}

.custom_btn {
  font-size: 16px;
  font-family: var(--font_roboto);
  width: 126px;
  border-radius: 80px;
  transition: var(--transition);
}

.custom_btn_order {
  width: 250px !important;
}

.btn_yellow {
  background-color: var(--primary_color);
  border: 2px solid var(--primary_color);
}

.download {
  display: none;
}

.print {
  display: none;
}

.btn_yellow:hover {
  background-color: transparent;
}

.btn_yellow_bordered {
  border: 2px solid var(--primary_color);
}

.btn_yellow_bordered:hover {
  background-color: var(--primary_color);
}

.heading {
  text-transform: uppercase;
  color: var(--black_color);
  font-weight: 500;
  display: inline-block;
}

.view_more {
  text-transform: uppercase;
  color: var(--black_color);
  font-weight: 500;
  position: relative;
  transition: var(--transition);
  float: right;
}

.view_more:hover {
  color: var(--secondary_color);
}

.view_more::before,
.view_more::after {
  content: "";
  position: absolute;
  left: 0;
  border: 2px solid var(--primary_color);
  bottom: -8px;
  transition: all ease-in-out 0.3s;
}

.view_more::before {
  width: 100%;
}

.view_more::after {
  width: 0;
}

.view_more:hover::after {
  border-color: var(--secondary_color);
  width: 100%;
}

.dropdown-item {
  font-size: 14px;
  font-weight: 600;
}

