.deals_sec .cards_wrapper>div {
  border-radius: 8px;
  overflow: hidden;
  transition: var(--transition);
  height: 280px;
}

.deals_sec .cards_wrapper>div:first-child {
  width: 450px;
}

.deals_sec .cards_wrapper>div:nth-child(2) {
