  position: relative;
  overflow: hidden;
}

.popular_products_sec .cards_wrapper>.card>.text_wrapper,
.trending_products_sec .cards_wrapper>.card>.text_wrapper {
  padding: 20px 30px;
}

.product_name {
  font-size: 19px;
