.super_rewards_sec {
  position: relative;
  margin-top: 60px;
}

.super_rewards_sec::before {
  content: "";
  width: 914px;
  height: 914px;
  position: absolute;
  top: -200px;
  left: -260px;
  background: linear-gradient(316deg,
      rgba(121, 187, 240, 0),
