  max-height: 480px;
  object-fit: cover;
}

.trending_products_sec {
  position: relative;
  margin-top: 70px;
}

.trending_products_sec::after {
  content: "";
  width: 633px;
  height: 633px;
  position: absolute;
  top: -200px;
  right: 0;
  background: linear-gradient(90deg,
      rgba(121, 187, 240, 0),
      rgba(23, 69, 106, 0.2));
