@import url(https://fonts.googleapis.com/css?family=Droid+Sans);

/* fontawesome */
[class*="fontawesome-"]:before {
  font-family: "FontAwesome", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

/*------ utiltity classes -----*/
.fl {
  float: left;
}

.fr {
  float: right;
}

/*its also known as clearfix*/
.group:before,
.group:after {
  content: "";
  display: table;
}

.group:after {
  clear: both;
}

.group {
  zoom: 1;
  /*For IE 6/7 (trigger hasLayout) */
}

body {
  background: #f2f2f2;
  font-family: "Droid Sans", sans-serif;
  line-height: 1;
  font-size: 16px;
}

.pricing-table {
  width: 80%;
  margin: 50px auto;
  text-align: center;
  padding: 10px;
  padding-right: 0;
}

.pricing-table .heading {
  color: #9c9e9f;
  text-transform: uppercase;
  font-size: 1.3rem;
  margin-bottom: 4rem;
}

.block {
  width: 30%;
  margin: 0 15px;
  overflow: hidden;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  /*    border: 1px solid red;*/
}

/*Shared properties*/
.title,
.pt-footer {
  color: #fefefe;
  text-transform: capitalize;
  line-height: 2.5;
  position: relative;
}

.content {
  position: relative;
  color: #fefefe;
  padding: 20px 0 10px 0;
}

/*arrow creation*/
.content:after,
.content:before,
.pt-footer:before,
.pt-footer:after {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.pt-footer:after,
.pt-footer:before {
  top: 0;
}

.content:after,
.pt-footer:after {
  border-color: rgba(136, 183, 213, 0);
  border-width: 5px;
  margin-left: -5px;
}

/*/arrow creation*/
.price {
